/* eslint no-restricted-globals: 1 */
import { MAX_LINES_PER_INDEX, TARGET_INDICES } from '../reducers/interpreter';
import { GCodeInterpreter, timeBetweenLines, generateBackPlotPoints } from '../gcode/interpreter';
import streamiterator from "streamiterator";
function isPercentLine(line) {
  return line === "%";
}
function isCommentLine(line) {
  const withoutN = line.replace(/^N\d+\s*/, "");
  return withoutN.startsWith("(") && withoutN.endsWith(")") || withoutN.startsWith(";");
}
async function* fileChunkIterator(fileData) {
  let offset = 0;
  const chunkSize = 1024 * 64;
  while (offset < fileData.size) {
    const blob = fileData.slice(offset, Math.min(offset + chunkSize, fileData.size));
    const chunk = await new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = e => {
        resolve(e.target.result);
      };
      reader.onerror = reject;
      reader.readAsArrayBuffer(blob);
    });
    yield new Uint8Array(chunk);
    offset += chunkSize;
  }
}

// First yield is the linesPerIndex
// yields a line of content, along with a percentage of the way through
async function* lineGenerator(fileData) {
  const textDecoder = new TextDecoder();
  let iterator = null;
  if (Array.isArray(fileData)) {
    yield Math.max(Math.min(Math.ceil(fileData.length / TARGET_INDICES), MAX_LINES_PER_INDEX), 1);
    // array of strings, one element for each line
    for (let i = 0; i < fileData.length; i++) {
      yield {
        line: fileData[i],
        progress: i / (fileData.length - 1) * 100
      };
    }
  } else {
    let bytes = 0;
    let contentLength = 100000;
    if (fileData instanceof File) {
      contentLength = fileData.size;
      iterator = fileChunkIterator(fileData);
    } else if (fileData.url) {
      const res = await fetch(fileData.url);
      for (const [name, value] of res.headers) {
        if (name.toLowerCase() === 'content-length') {
          contentLength = parseInt(value);
          break;
        }
      }
      iterator = streamiterator(res.body.getReader());
    } else if (typeof fileData === "string") {
      const encoder = new TextEncoder();
      const encodedString = encoder.encode(fileData);
      contentLength = encodedString.length;
      iterator = [encodedString];
    } else if (fileData.byteLength) {
      // ArrayBuffer like object

      contentLength = fileData.byteLength;
      iterator = [new Uint8Array(fileData)];
    }
    const approxLineCount = contentLength / 53; // ~53 bytes per line, this is arbitrary, but was calculated from a large file we test with
    // Since we're not parsing the whole file before hand we don't know the number of lines
    // ahead of time. We guess here, so that we can choose how many states to save while interpreting.
    yield Math.max(Math.min(Math.ceil(approxLineCount / TARGET_INDICES), MAX_LINES_PER_INDEX), 1);
    let leftOver = new Uint8Array(0);
    for await (const chunk of iterator) {
      let startIndex = 0;
      let endIndex = chunk.indexOf(10); // find next index of newline character
      if (endIndex > -1) {
        if (leftOver.length > 0) {
          const lineBuffer = new Uint8Array(leftOver.length + endIndex);
          lineBuffer.set(leftOver);
          lineBuffer.set(chunk.subarray(0, endIndex), leftOver.length);
          bytes += lineBuffer.length + 1;
          yield {
            line: textDecoder.decode(lineBuffer).replace(/\r/, ""),
            progress: bytes / contentLength * 100
          };
        } else {
          const lineBuffer = chunk.subarray(startIndex, endIndex);
          bytes += lineBuffer.length + 1;
          yield {
            line: textDecoder.decode(lineBuffer).replace(/\r/, ""),
            progress: bytes / contentLength * 100
          };
        }
        while (endIndex > -1) {
          startIndex = endIndex + 1;
          endIndex = chunk.indexOf(10, startIndex);
          if (endIndex > -1) {
            const lineBuffer = chunk.subarray(startIndex, endIndex);
            bytes += lineBuffer.length + 1;
            yield {
              line: textDecoder.decode(lineBuffer).replace(/\r/, ""),
              progress: bytes / contentLength * 100
            };
          } else {
            leftOver = chunk.subarray(startIndex);
          }
        }
      } else {
        const newLeftOver = new Uint8Array(leftOver.length + chunk.length);
        newLeftOver.set(leftOver);
        newLeftOver.set(chunk, leftOver.length);
        leftOver = newLeftOver;
      }
    }
  }
}
self.onmessage = async msg => {
  const {
    machineState,
    file
  } = msg.data;
  const generator = lineGenerator(file);
  const interpreter = new GCodeInterpreter({
    machineState
  });
  let states = [];
  let times = [];
  let messages = [];
  let points = [];
  let lines = [];
  let toolPaths = [0];
  const linesPerIndex = (await generator.next()).value;
  let totalTime = 0;
  let currentLine = 0;
  let currentState = machineState;
  let lastLineWasCommentLine = true;

  // Get an update out as fast as possible, so issue update after first N
  // lines. After that update every updateEvery lines.
  let updatedFirstN = false;
  const N = 50;
  const updateEvery = 1000;
  for await (const {
    line,
    progress
  } of generator) {
    const nextState = interpreter.interpret(line, messages, currentLine);
    const time = timeBetweenLines(currentState, nextState, line, currentLine);
    const lineIsComment = isCommentLine(line) || isPercentLine(line);
    totalTime += time;

    // append data to various arrays, which will get sent and reset at the next update
    // every line is recorded, along with the simulated time when that line has completed running
    lines.push(line);
    times.push(totalTime);
    if (!lastLineWasCommentLine && lineIsComment) {
      toolPaths.push(currentLine);
    }
    lastLineWasCommentLine = lineIsComment;

    // Only record the full state every linesPerIndex lines
    // This is a way to save memory for huge programs at the expense of playback performance.
    if (currentLine % linesPerIndex === 0) {
      states.push(nextState);
    }

    // mutates points array
    if (nextState.motion.issuedMotionCommand) {
      generateBackPlotPoints(points, currentState, nextState, totalTime, time, currentLine);
    }
    if (!updatedFirstN && lines.length >= N || lines.length > updateEvery) {
      updatedFirstN = true;
      self.postMessage({
        progress,
        lines,
        states,
        times,
        toolPaths,
        messages,
        points,
        linesPerIndex
      });

      // clear our arrays to prepare for the next update
      lines = [];
      states = [];
      times = [];
      messages = [];
      points = [];
      toolPaths = [];
    }
    currentState = nextState;
    currentLine++;
  }
  if (lines.length > 0) {
    self.postMessage({
      progress: 100,
      lines,
      states,
      times,
      toolPaths,
      messages,
      points,
      linesPerIndex
    });
  }
  self.close();
};